<template>
  <div id="app">
    <component-header></component-header>
    <component-menu></component-menu>
    <component-section></component-section>
    <component-footer></component-footer>
  </div>
</template>

<script>
import componentHeader from './components/common/header'
import componentMenu from './components/common/menu'
import componentSection from './components/common/section'
import componentFooter from './components/common/footer'
export default {
  name: 'App',
  components: {
    'componentHeader': componentHeader,
    'componentMenu': componentMenu,
    'componentSection': componentSection,
    'componentFooter': componentFooter
  }
}
</script>

<style>
html {
  height: 100%;
  font-size: 62.5%;
}
body {
  margin: 0px;
  height: 100%;
  background-color: white;
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.4rem;
  line-height: 1.8;
  color: #969aa1;
}
body * {
  margin: 0px;
  padding: 0px;
}
ul {
  list-style: none;
}
#app {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



