<template>
<div class="pagination">
  <a class="prev"><i class="material-icons">
    keyboard_arrow_left
  </i>PREV PAGE</a>
  <a class="focused">1</a>
  <a>2</a>
  <a>3</a>
  <a>4</a>
  <a class="next disabled">NEXT PAGE<i class="material-icons">
    keyboard_arrow_right
  </i></a>
</div>
</template>

<script>
export default {
  name: 'pagination'
}
</script>

<style scoped>
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem 9rem;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  background-color: transparent;
}

.pagination > a > i.material-icons {
  font-size: 2rem;
}
.pagination > .prev > i.material-icons {
  margin-right: 0.5rem;
}

.pagination > .next > i.material-icons {
  margin-left: 0.5rem;
}
.pagination > a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border-radius: 0.3rem;
  border: 1px solid transparent;
  color: #33363a;
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 4rem;
  text-transform: uppercase;
  cursor: pointer;
}

.pagination > a.focused {
  border: 1px solid #3fb58b;
  color: #3fb58b;
}

.pagination > a.disabled, .pagination > a.disabled:hover {
  color: #999;
  cursor: default;
}

.pagination > a:hover {
  color: #3fb58b;
}

.pagination > .prev, .pagination > .next {
  width: auto;
}

.pagination > .prev {
  margin-right: auto;
}

.pagination > .next {
  margin-left: auto;
}
</style>



